import styled from "styled-components";

export const H1 = styled.h1`
  font-weight: bold;
  font-size: 60px;
  text-align: right;
`;

export const H2 = styled.h2`
  font-weight: bold;
  font-size: 40px;
  text-align: right;
`;

export const H3 = styled.h3`
  font-weight: bold;
  font-size: 30px;
  text-align: right;
`;

export const BodyIntro = styled.p`
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  text-align: right;
`;

export const BodyMain = styled.p`
  font-weight: normal;
  font-size: 20px;
  line-height: 140%;
  text-align: right;
`;

export const MediumText = styled.p`
  font-weight: normal;
  font-size: 17px;
  line-height: 130%;
  text-align: right;
`;

export const Caption = styled.p`
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  text-align: right;
`;

export const Caption2 = styled.p`
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: right;
`;

export const SmallText = styled.p`
  font-weight: normal;
  font-size: 13px;
  line-height: 130%;
  text-align: right;
`;

export const SmallText2 = styled.p`
  font-weight: 600;
  font-size: 13px;
  line-height: 130%;
  text-transform: uppercase;
  text-align: right;
`;
